import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'dayname'
})
export class DayNamePipe implements PipeTransform {
    
    transform(value: string) {
        if (!value) 
            return null;
        let valnum = parseInt(value);
        value = String(valnum);

        let initialChar = "";
        if (value.length > 1) {
            initialChar = value.charAt(0);
        }
        let finalChar = value.charAt(value.length - 1);

        if (initialChar != "1") {
            switch (finalChar) {
                case "1":
                    return value + "st";
                    break;
                case "2":
                    return value + "nd";
                    break;
                case "3":
                    return value + "rd";
                    break;
                case "4":
                case "5":
                case "6":
                case "7":
                case "8":
                case "9":
                    return value + "th";
            }
        } else {
            return value + "th";
        }        
    }
}