import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PillHistoryComponent } from '../pill-history/pill-history.component';
import { DayNamePipe } from 'src/dayname.pipe';
import { MessageService } from 'src/message.service';

@Component({
    selector: 'app-history-day',
    templateUrl: './history-day.component.html',
    styleUrls: ['./history-day.component.css']
})
export class HistoryDayComponent implements OnInit {

    @Input() currentDate: String;
    @Input() month: String;
    @Input() day: String;
    @Input() dayOfWeek: String;
    @Input() dayText: String;
    @Input() pills = [];
    
    constructor(private messageService: MessageService) { }

    ngOnInit() {
    }

    onClick($event, pillId) {
        $event.stopPropagation();
        this.messageService.sendMessage({
            weekNum: 0,
            doShowForm: false,
            currentDate: this.currentDate,
            deletePillId: pillId
        });
    }
}
