import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { PillsService } from '../pills.service';
import { PillHistoryService } from '../pillhistory.service';

@Component({
    selector: 'app-add-pill',
    templateUrl: './add-pill.component.html',
    styleUrls: ['./add-pill.component.css']
})
export class AddPillComponent implements OnInit {

    @Input() currentDate: string;
    @Input() weekNum: number
    @Output() change = new EventEmitter();
    pills = [];

    constructor(private pillsService: PillsService, private pillHistoryService: PillHistoryService) {
        this.pills = pillsService.getPills();
    }

    ngOnInit() {
        console.log("currentDate at 7: ", this.currentDate);
    }

    onClick($event, pill_taken, qty) {
        console.log("did click");
        console.log({
            weekNum: this.weekNum,
            currentDate: this.currentDate,
            pill_id: parseInt(pill_taken.value),
            qty: parseInt(qty.value)
        })
        this.change.emit({
            weekNum: this.weekNum,
            currentDate: this.currentDate,
            pill_id: parseInt(pill_taken.value),
            qty: parseInt(qty.value)
        });
    }
}
