import { Component, OnDestroy, OnChanges } from '@angular/core';
import { PillHistoryService } from './pillhistory.service';
import { MessageService } from 'src/message.service';
import { Subscription } from 'rxjs';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent implements OnDestroy, OnChanges {
    title = 'pill-history';

    message: any;
    subscription: Subscription;

    addPillFormShowing = false;
    pillHistory = [];
    weekNames = [];
    currentDayInForm = "1969-12-31";
    weekNum: number;

    
    constructor(private pillHistoryService: PillHistoryService, private messageService: MessageService) {

        this.pillHistory = this.pillHistoryService.getPillHistory();
        this.weekNames = this.pillHistoryService.getWeekNames();

        this.subscription = this.messageService.getMessage().subscribe(message => { 
            if (message.doShowForm) {

                this.addPillFormShowing = true;

            } else if (message.deletePillId > 0) {

                this.pillHistoryService.setPillHistoryItem(this.weekNum, this.currentDayInForm, message.deletePillId, 0);
            }
            console.log("message object: ", message);
            this.weekNum = message.weekNum;
            this.currentDayInForm = message.currentDate;
        });
    }

    ngOnChanges() {
        this.pillHistory = this.pillHistoryService.getPillHistory();
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    onSubmitAddPill(eventArgs: SubmitAddPillEventArgs) {
        console.log("weekNum: ", eventArgs.weekNum);
        console.log("currentDate: ", eventArgs.currentDate);
        console.log("pill_taken: ", eventArgs.pill_id);
        console.log("qty: ", eventArgs.qty);

        this.pillHistoryService.setPillHistoryItem(eventArgs.weekNum, eventArgs.currentDate, eventArgs.pill_id, eventArgs.qty)
        this.addPillFormShowing = false;
    }
}

export interface MessageServiceEventArgs {
    weekNum: number,
    doShowForm: boolean,
    currentDate: String,
    deletePillId: number
}

export interface SubmitAddPillEventArgs {
    weekNum: number,
    currentDate: string,
    pill_id: number,
    qty: number
}
