import { Component, OnInit, Input } from '@angular/core';
import { MessageService } from 'src/message.service';

@Component({
    selector: 'app-pill-history',
    templateUrl: './pill-history.component.html',
    styleUrls: ['./pill-history.component.css']
})
export class PillHistoryComponent implements OnInit {

    @Input() pill_history;
    @Input() week_names;


    constructor(private messageService: MessageService) {

    }

    ngOnInit() {
        
    }

    onClickDay($event, weekNum: number, currentDate: String) {
        console.log("did click onclick");
        
        this.messageService.sendMessage({
            weekNum: weekNum,
            doShowForm: true,
            currentDate: currentDate,
            deletePillId: 0
        });
    }
}
